import * as React from "react";
import { graphql, PageProps } from "gatsby";
import Img from "gatsby-image";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import Button from "../components/Button";
import { SEO } from "../components/SEO";

const ContactPage: React.FC<PageProps> = (props) => {
  const data = props.data as any;
  const backgroundImage = data.backgroundImage;

  return (
    <main>
      <div className="relative">
        <Img
          fluid={backgroundImage.childImageSharp.fluid}
          alt={backgroundImage.name}
          className="!absolute z-[-1] w-full h-full !object-cover !object-center"
        />
        <div className="blur-background">
          <Navbar />
        </div>
        <div
          className="py-16 bg-white text-[#262A2D] container select-none"
          id="services"
        >
          <h2 className="text-2xl">Your response submitted successfully!</h2>
          <div className="mb-10"></div>
          <span className="block mb-3">
            We'll shortly contact you for your enquiry.
          </span>
          <div className="mb-10"></div>
          <Button to="/" primary={false}>
            Back to Home
          </Button>
        </div>
      </div>
      <Footer />
    </main>
  );
};

export default ContactPage;

export const Head = () => <SEO title={`Thank you | VPTronics`} />;

export const query = graphql`
  query {
    backgroundImage: file(relativePath: { eq: "solar-farm.jpg" }) {
      name
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
